import React, { useState, useRef, forwardRef, useImperativeHandle } from 'react';

import PlayerDeckCard from './playerDeckCard.js';

import deleteIcon from '../img/delete-icon.png';
import deleteButton from '../img/delete-button.png';
import copyIcon from '../img/copy-icon.png';
import copyButton from '../img/copy-button.png';
import elixirImg from '../img/elixir.png';
import elixirCycleImg from '../img/elixir-cycle.png';
import cycleImg from '../img/cycle.png';

import cards from '../utils/card-data.js';

const PlayerDeck = forwardRef((props, ref) => {

	const [currentMode, setCurrentMode] = useState('player');

	const baseArray = [0, 1, 2, 3, 4, 5, 6, 7];	
	const [deckCards, setDeckCards] = useState([-1, -1, -1, -1, -1, -1, -1, -1]);

	const playerCardRefArray = [useRef(), useRef(), useRef(), useRef(), useRef(), useRef(), useRef(), useRef()];

	useImperativeHandle(ref, () => ({
		changeCurrentMode: (newMode) => {
			setCurrentMode(newMode);
			for (let i = 0; i < playerCardRefArray.length; i ++) {
				playerCardRefArray[i].current.changeCurrentMode(newMode);
			}			
		},

		addCardToDeck: (cardNum) => {
			let availablePosition = deckCards.indexOf(-1);

			if (availablePosition !== -1) {
				deckCards[availablePosition] = cardNum;
				setDeckCards([...deckCards]);
				return true;
			}

			return false;
		},

		isDeckFull: () => {		

			return deckCards.every(element => element !== -1);
		}
		
	}));

	const removeCardFromDeck = (cardNum) => {
		let cardPosition = deckCards.indexOf(cardNum);

		if (cardPosition === -1) return;

		// deckCards.splice(cardPosition, 1);
		deckCards[cardPosition] = -1
		setDeckCards([...deckCards]);
		props.funcUtils.removeFromDeckAndMakeAvailable(cardNum);
	}

	const getDeckCostArray = () => {

		let deckCostArray = [];
		for (let i = 0; i < deckCards.length; i ++) {
			deckCostArray.push(cards['items'][deckCards[i]]['cost'])
		}

		return deckCostArray;
	}

	const getDeckCardsIDArray = () => {

		let deckCardsIDArray = [];
		for (let i = 0; i < deckCards.length; i ++) {
			deckCardsIDArray.push(cards['items'][deckCards[i]]['id'])
		}

		return deckCardsIDArray;
	}

	const isDeckFull = () => {		
			return deckCards.every(element => element !== -1);
	}	

	const getDeckAvgElixir = () => {
		let deckCostArray = getDeckCostArray();

		const reducer = (acc, cur) => acc + cur;
		let result = deckCostArray.reduce(reducer);

		return (result / 8).toFixed(1);
	}

	const getCycleCost = () => {
		let deckCostArray = getDeckCostArray();

		deckCostArray = deckCostArray.sort();

		return (deckCostArray[0] + deckCostArray[1] + deckCostArray[2] + deckCostArray[3]).toFixed(1);
	}	

	const openOfficialDeckLink = () => {
		let deckCardsIDArray = getDeckCardsIDArray();

		let officialLink = 'https://link.clashroyale.com/deck/en?deck='

		deckCardsIDArray.map((cardId, idx) => officialLink += (idx !== 0 ? ';' : '') + cardId)


		window.open(officialLink)
	}

	const deleteDeck = () => {
		if (currentMode === 'enemy') return;

		deckCards.map(cardNum => props.funcUtils.removeFromDeckAndMakeAvailable(cardNum))
		setDeckCards([-1, -1, -1, -1, -1, -1, -1, -1]);
	}

	return(
		<div style={{backgroundColor: '#79D9E3', height: '168px', margin: '15px', padding: '10px', textAlign: 'center'}} className="row roundSquare">
			<div className="row">
				<div className="eleven columns">
					<div className="row">
						{ [0, 1, 2, 3].map(e => <PlayerDeckCard key={String(e)} ref={playerCardRefArray[e]} position={deckCards[e]} funcUtils={{'removeCardFromDeck': removeCardFromDeck}}/>) }
						
					</div>	
					<div className="row">
						{ [4, 5, 6, 7].map(e => <PlayerDeckCard key={String(e)} ref={playerCardRefArray[e]} position={deckCards[e]} funcUtils={{'removeCardFromDeck': removeCardFromDeck}}/>) }
					</div>	
				</div>
				<div className="one column">
					<div className="row" style={{'cursor': 'pointer', height: '72px', marginBottom: '1px', marginLeft: '-30px'}} onClick={isDeckFull() ? openOfficialDeckLink : null}>						<img style={{marginTop: '15px', width: '100%', filter: isDeckFull() ? '' : 'grayscale(1.0)'}} src={copyButton} />
					</div>
					<div className="row" style={{'cursor': 'pointer', marginTop: '11px', marginLeft: '-30px'}} onClick={isDeckFull() ? deleteDeck : null}>
						<img style={{width: '100%', filter: !isDeckFull() || currentMode === 'enemy' ? 'grayscale(1.0)' : ''}} src={deleteButton} />
					</div>
				</div>								
			</div>		
			<div className="row">
				<div className="eleven columns">
					<div className="row">
						<div className="four columns">
						</div>
						<div className="four columns" style={{backgroundColor: '#3cd', borderRadius: '20px', marginLeft: '50px'}}>
							<span style={{float: 'right', fontFamily: 'Arial Black'}}> 
								<img src={elixirImg} style={{width: '25%', float: 'right'}}/> 
								<span style={{color: '#3E7ADE', float: 'right'}}> { isDeckFull() ? getDeckAvgElixir() : '??'} </span> 
							</span>
						</div>
						<div className="four columns" style={{backgroundColor: '#3cd', borderRadius: '20px'}}>
							<span style={{float: 'right', fontFamily: 'Arial Black'}}> 
								<img src={elixirCycleImg} style={{width: '30%', float: 'left'}}/> 
								<span style={{color: '#3E7ADE', float: 'left', marginLeft: '6px'}}> { isDeckFull() ? getCycleCost() : '??'} </span> 
							</span>
						</div>						
					</div>
				</div>
				<div className="one column">
					
				</div>
			</div>	
		</div>
	);
});

export default PlayerDeck;