import React, { useState, useRef, useEffect, forwardRef, useImperativeHandle  } from 'react';

import EnemyCard from './enemyCard.js';
import cards from '../utils/card-data.js';

import elixirImg from '../img/elixir.png';
import elixirCycleImg from '../img/elixir-cycle.png';

const EnemyDeck = forwardRef((props, ref) => {

	const [hasCopiedLink, setHasCopiedLink] = useState(false);
	const [inputValue, setInputValue] = useState('');
	const [cardArrayPositions, setCardArrayPositions] = useState([]);
	const [isDeckTransparent, setIsDeckTransparent] = useState(false);
	const [highlightedCardNum, setHighlightedCardNum] = useState(null);

	const [repeatedCardsWithDeckList, setRepeatedCardsWithDeckList] = useState([]);
	const [currentMode, setCurrentMode] = useState('player');

	const copyDeckInputRef = useRef();

	useEffect(() => {		

		if (hasCopiedLink) {			

			if (props.deckNum === 2 && !isDeckTransparent) {
				let repeatedCardNumDeck1 = findRepeatedCard(props.otherDecksRef[0]);

				console.log(repeatedCardNumDeck1)

				if (repeatedCardNumDeck1 !== -1) {
					changeToHighlight(repeatedCardNumDeck1, props.otherDecksRef[0]);
					// repeatedCardsWithDeckList.push(1);
					// setRepeatedCardsWithDeckList(repeatedCardsWithDeckList);
					// console.log(cardArrayPositions)
					// console.log(props.otherDecksRef[0].current.getCurrentDeck())					
				}
				
			} else if (props.deckNum === 3 && !isDeckTransparent) {
				let repeatedCardNumDeck1 = findRepeatedCard(props.otherDecksRef[0]);

				if (repeatedCardNumDeck1 !== -1) {
					changeToHighlight(repeatedCardNumDeck1, props.otherDecksRef[0]);
					// repeatedCardsWithDeckList.push(1);
					// setRepeatedCardsWithDeckList(repeatedCardsWithDeckList);
				}

				let repeatedCardNumDeck2 = findRepeatedCard(props.otherDecksRef[1]);

				if (repeatedCardNumDeck2 !== -1) {
					changeToHighlight(repeatedCardNumDeck2, props.otherDecksRef[1]);
					// repeatedCardsWithDeckList.push(2);
					// setRepeatedCardsWithDeckList(repeatedCardsWithDeckList);
				}
			}
		}
	});

	useImperativeHandle(ref, () => ({
		
		getCurrentDeck: () => {
			return cardArrayPositions;
		},

		getIsTransparent: () => {
			return isDeckTransparent;
		},

		setIsDeckTransparent: (transparent) => {
			setIsDeckTransparent(transparent);	
		},

		highlightCard: (cardNum) => {
			setIsDeckTransparent(true);
			setHighlightedCardNum(cardNum);			
		},

		adjustPairedDeckTransparency: () => {
			if (repeatedCardsWithDeckList.length > 0) {

			} else {
				setIsDeckTransparent(false);
			}
		},

		changeCurrentMode: (newMode) => {
			setCurrentMode(newMode);
		}
	}));

	const changeToHighlight = (repeatedCard, deckRef) => {
		setIsDeckTransparent(true);		
		deckRef.current.setIsDeckTransparent(true);
		props.funcUtils.changeRepeatedCardsList('add', repeatedCard);
		console.log(cardArrayPositions)
		console.log(props.otherDecksRef[0].current.getCurrentDeck())
		// setHighlightedCardNum(repeatedCard);		
		
	}

	const findRepeatedCard = (deckRef) => {

		let analysedDeck = deckRef.current.getCurrentDeck();

		for (let i = 0; i < cardArrayPositions.length; i ++) {
			for (let j = 0; j < analysedDeck.length; j ++) {
				if (cardArrayPositions[i] === analysedDeck[j]) {
					return cardArrayPositions[i];
				}					
			}
		}

		return -1;
	}

	const cardIdToCardPosition = (id) => {		
		
		for (let i = 0; i < cards.items.length; i ++) {						
			if (cards.items[i].id === id) {				
				return i;
			}
		}
	}

	const validateInputLink = (value) => {				

		if (props.deckNum === 2 && props.otherDecksRef[0].current.getCurrentDeck().length === 0) {
			alert('Please copy decks from top slot to bottom');
			copyDeckInputRef.current.value ='';			
			return;
		}

		if (props.deckNum === 3 && (props.otherDecksRef[0].current.getCurrentDeck().length === 0
								|| props.otherDecksRef[1].current.getCurrentDeck().length === 0)) {
			alert('Please copy decks from top slot to bottom');
			copyDeckInputRef.current.value ='';			
			return;
		}
		
		if (value.search('https://link.clashroyale.com/deck/') === -1) return;

		let sepPos = value.search('=');

		if (sepPos === -1) return;

		let cardIds = value.substring(sepPos + 1).split(';').map(Number);		
		let cap = cardIds.map(id => cardIdToCardPosition(id));		

		setHasCopiedLink(true);
		setCardArrayPositions(cap);

		props.cardSelectorRef.current.changeEnemySelectionList('add', cap);
	}	

	const deleteDeck = () => {

		if (props.deckNum === 1 && (props.otherDecksRef[0].current.getCurrentDeck().length > 0
								|| props.otherDecksRef[1].current.getCurrentDeck().length > 0)) {
			alert('Please delete decks from bottom to top');
			return;
		}

		if (props.deckNum === 2 && props.otherDecksRef[1].current.getCurrentDeck().length > 0) {								 
			alert('Please delete decks from bottom to top');
			return;
		}

		let hasCommonCardWithDeck1 = false;
		let hasCommonCardWithDeck2 = false;
		let deck1HasCommonCardWithDeck2 = false;

		if (props.deckNum === 3) {
			let deck1 = props.otherDecksRef[1].current.getCurrentDeck();
			let deck0 = props.otherDecksRef[0].current.getCurrentDeck();
			let cardRemoveList = [];
			

			for (let i = 0; i < deck1.length; i ++) {
				if (cardArrayPositions.indexOf(deck1[i]) !== -1) {
					if (!deck0.includes(deck1[i])) {
						cardRemoveList.push(deck1[i]);						
					}
					hasCommonCardWithDeck2 = true;
				}
			}			

			for (let i = 0; i < deck0.length; i ++) {
				if (cardArrayPositions.indexOf(deck0[i]) !== -1) {
					if (!deck1.includes(deck0[i])) {
						cardRemoveList.push(deck0[i]);						
					}					
					hasCommonCardWithDeck1 = true;
				}				
			}

			for (let i = 0; i < deck0.length; i ++) {
				if (deck0.indexOf(deck1[i]) !== -1) {
					deck1HasCommonCardWithDeck2 = true;
					break;
				}
			}

			if (!deck1HasCommonCardWithDeck2) {				
				if (hasCommonCardWithDeck1) props.otherDecksRef[0].current.setIsDeckTransparent(false);
				if (hasCommonCardWithDeck2) props.otherDecksRef[1].current.setIsDeckTransparent(false);
			}

			cardRemoveList.map(e => {
				if (deck0.includes(e) || deck1.includes(e)) {
					cardArrayPositions.splice(cardArrayPositions.indexOf(e), 1);
				}
			});

			cardArrayPositions.map(e => {
				if (deck0.includes(e) || deck1.includes(e)) {
					cardArrayPositions.splice(cardArrayPositions.indexOf(e), 1);
				}
			})
/*
			for (let i = 0; i < cardArrayPositions.length; i ++) {
				if (cardRemoveList.indexOf(cardArrayPositions[i]) !== -1 &&
								  (!deck0.includes(cardArrayPositions[i]) && !deck1.includes(cardArrayPositions[i]))
								  ) {
					console.log('indice da carta a ser removida: ' + String(cardArrayPositions.indexOf(cardArrayPositions[i])))
					console.log('carta a ser removida: ' + String(cardArrayPositions[i]) + ' ' + cards['items'][cardArrayPositions[i]]['name'])
					console.log(cardArrayPositions)
					cardArrayPositions.splice(cardArrayPositions.indexOf(cardArrayPositions[i]), 1);					
					console.log(cardArrayPositions)
				}
			}
*/
			cardRemoveList.map(e => console.log(cards['items'][e]['name']))

			props.cardSelectorRef.current.changeEnemySelectionList('remove', cardArrayPositions);
			cardRemoveList.map(e => props.funcUtils.changeRepeatedCardsList('remove', e));

		} else if (props.deckNum === 2) {
			let deck0 = props.otherDecksRef[0].current.getCurrentDeck();
			let cardRemoveList = [];

			for (let i = 0; i < deck0.length; i ++) {
				if (cardArrayPositions.indexOf(deck0[i]) !== -1) {
					cardRemoveList.push(deck0[i]);
				}
			}

			for (let i = 0; i < cardArrayPositions.length; i ++) {
				if (cardRemoveList.indexOf(cardArrayPositions[i]) !== -1) {						
					cardArrayPositions.splice(cardArrayPositions.indexOf(cardArrayPositions[i]), 1);					
				}
			}

			props.cardSelectorRef.current.changeEnemySelectionList('remove', cardArrayPositions);
			props.otherDecksRef[0].current.setIsDeckTransparent(false);
			cardRemoveList.map(e => props.funcUtils.changeRepeatedCardsList('remove', e));			

		} else if (props.deckNum === 1) {

			props.cardSelectorRef.current.changeEnemySelectionList('remove', cardArrayPositions);
		}

		// props.cardSelectorRef.current.changeEnemySelectionList('remove', cardArrayPositions);
		setCardArrayPositions([]);
		setHasCopiedLink(false);		

		if (isDeckTransparent) setIsDeckTransparent(false);		

		// TODO
		// checar quais cartas sao iguais em quais decks
		// if (carta esta em um deck e no outro nao)
		// setar para transparente ou nao de acordo
	}

	const getDeckCostArray = () => {

		let deckCostArray = [];
		for (let i = 0; i < cardArrayPositions.length; i ++) {
			deckCostArray.push(cards['items'][cardArrayPositions[i]]['cost'])
		}

		return deckCostArray;
	}

	const getDeckAvgElixir = () => {
		let deckCostArray = getDeckCostArray();

		const reducer = (acc, cur) => acc + cur;
		let result = deckCostArray.reduce(reducer);

		return (result / 8).toFixed(1);
	}

	const getCycleCost = () => {
		let deckCostArray = getDeckCostArray();

		deckCostArray = deckCostArray.sort();

		return (deckCostArray[0] + deckCostArray[1] + deckCostArray[2] + deckCostArray[3]).toFixed(1);
	}

	return(
			<div style={{backgroundColor: '#df7177', height: '230px', margin: '15px', padding: '10px'}} className="row roundSquare">
				{
					isDeckTransparent ?
						<div style={{top: '20', right: '0', marginTop: '40px', marginRight: '50px', position: 'absolute', borderRadius: '100px', backgroundColor: '#F0E130', fontSize: '40px', width: '70px', height: '70px'}}>
							<div style={{textAlign: 'center', paddingTop: '5px', color: '#323'}}>
								!
							</div>
						</div>
					: ''
				 }				
				<div className="row">
					<div style={{textAlign: 'center'}} className="row">
						<EnemyCard position={hasCopiedLink ? cardArrayPositions[0] : -1} transparent={isDeckTransparent && !props.funcUtils.getRepeatedCardsList().includes(cardArrayPositions[0]) } />
						<EnemyCard position={hasCopiedLink ? cardArrayPositions[1] : -1} transparent={isDeckTransparent && !props.funcUtils.getRepeatedCardsList().includes(cardArrayPositions[1]) } />
						<EnemyCard position={hasCopiedLink ? cardArrayPositions[2] : -1} transparent={isDeckTransparent && !props.funcUtils.getRepeatedCardsList().includes(cardArrayPositions[2]) } />
						<EnemyCard position={hasCopiedLink ? cardArrayPositions[3] : -1} transparent={isDeckTransparent && !props.funcUtils.getRepeatedCardsList().includes(cardArrayPositions[3]) } />
					</div>
					<div style={{textAlign: 'center'}} className="row">
						<EnemyCard position={hasCopiedLink ? cardArrayPositions[4] : -1} transparent={isDeckTransparent && !props.funcUtils.getRepeatedCardsList().includes(cardArrayPositions[4]) } />
						<EnemyCard position={hasCopiedLink ? cardArrayPositions[5] : -1} transparent={isDeckTransparent && !props.funcUtils.getRepeatedCardsList().includes(cardArrayPositions[5]) } />
						<EnemyCard position={hasCopiedLink ? cardArrayPositions[6] : -1} transparent={isDeckTransparent && !props.funcUtils.getRepeatedCardsList().includes(cardArrayPositions[6]) } />
						<EnemyCard position={hasCopiedLink ? cardArrayPositions[7] : -1} transparent={isDeckTransparent && !props.funcUtils.getRepeatedCardsList().includes(cardArrayPositions[7]) } />
					</div>
					{ hasCopiedLink ?
						<div className="row">
							<div className="row" style={{marginBottom: '10px'}}>
								<div className="eleven columns">
									<div className="row">
										<div className="four columns">
										</div>
										<div className="four columns" style={{backgroundColor: '#d3454e', borderRadius: '20px', marginLeft: '60px'}}>
											<span style={{float: 'right', fontFamily: 'Arial Black'}}> 
												<img src={elixirImg} style={{width: '25%', float: 'right'}}/> 
												<span style={{color: '#A1353C', float: 'right'}}> { getDeckAvgElixir() } </span> 
											</span>
										</div>
										<div className="four columns" style={{backgroundColor: '#d3454e', borderRadius: '20px'}}>
											<span style={{float: 'right', fontFamily: 'Arial Black'}}> 
												<img src={elixirCycleImg} style={{width: '30%', float: 'left'}}/> 
												<span style={{color: '#A1353C', float: 'left', marginLeft: '6px'}}> { getCycleCost() } </span> 
											</span>
										</div>						
									</div>
								</div>
								<div className="one column">
									
								</div>
							</div>
							<div style={{textAlign: 'center'}}>
								<button disabled={currentMode === 'player' ? true : false} onClick={deleteDeck} style={{height: '30px', backgroundColor: '#f8c828', border: 'none', color: '#333', padding: '2px 50px 2px 50px', cursor: currentMode === 'player' ? 'not-allowed' : ''}}>Delete Deck!</button>
							</div>
						</div>													
					 : 
					 	<div style={{textAlign: 'center'}}>
							<input disabled={currentMode === 'player' ? true : false} ref={copyDeckInputRef} className="copyLinkInput" style={{width: '70%', marginBottom: '10px', cursor: currentMode === 'player' ? 'not-allowed' : ''}}ype="text" placeholder="Paste deck link here" onChange={event => validateInputLink(event.target.value)} />						
						</div>
					}
				</div>
				
			</div>
		);
});


export default EnemyDeck;