import React, { useState, useRef, forwardRef, useImperativeHandle } from 'react';

import EnemyDeck from './enemyDeck.js';

const EnemyGrid = forwardRef((props, ref) => {

	const enemyDeck1Ref = useRef();
	const enemyDeck2Ref = useRef();
	const enemyDeck3Ref = useRef();

	const [currentMode, setCurrentMode] = useState('player');
	const [repeatedCardsList, setRepeatedCardsList] = useState([]);

	useImperativeHandle(ref, () => ({
		changeCurrentMode: (newMode) => {
			setCurrentMode(newMode);
			enemyDeck1Ref.current.changeCurrentMode(newMode);
			enemyDeck2Ref.current.changeCurrentMode(newMode);
			enemyDeck3Ref.current.changeCurrentMode(newMode);
		}
	}));

	const changeRepeatedCardsList = (action, card) => {
		if (action === 'add') {
			if (repeatedCardsList.includes(card)) return;

			repeatedCardsList.push(card);
			console.log(repeatedCardsList)
			setRepeatedCardsList([...repeatedCardsList]);
		} else if (action === 'remove') {
			let cardPos = repeatedCardsList.indexOf(card);

			console.log(cardPos)

			if (cardPos === -1) return;
			console.log(repeatedCardsList)
			repeatedCardsList.splice(cardPos, 1);
			console.log(repeatedCardsList)
			setRepeatedCardsList([...repeatedCardsList]);
		} else if (action === 'clean') {
			setRepeatedCardsList([]);
		}
	}

	const getRepeatedCardsList = () => {
		return repeatedCardsList;
	}

	const test = () => {	
		enemyDeck1Ref.current.test();
		console.log(enemyDeck1Ref.current.getCurrentDeck());
	}

	return (
		<div style={{backgroundColor: '#d3454e'}} className="roundSquare">
			<EnemyDeck deckNum={1} ref={enemyDeck1Ref} cardSelectorRef={props.cardSelectorRef} otherDecksRef={[enemyDeck2Ref, enemyDeck3Ref]} funcUtils={{'changeRepeatedCardsList': changeRepeatedCardsList, 'getRepeatedCardsList': getRepeatedCardsList}} />
			<EnemyDeck deckNum={2} ref={enemyDeck2Ref} cardSelectorRef={props.cardSelectorRef} otherDecksRef={[enemyDeck1Ref, enemyDeck3Ref]} funcUtils={{'changeRepeatedCardsList': changeRepeatedCardsList, 'getRepeatedCardsList': getRepeatedCardsList}} />
			<EnemyDeck deckNum={3} ref={enemyDeck3Ref} cardSelectorRef={props.cardSelectorRef} otherDecksRef={[enemyDeck1Ref, enemyDeck2Ref]} funcUtils={{'changeRepeatedCardsList': changeRepeatedCardsList, 'getRepeatedCardsList': getRepeatedCardsList}} />
		</div>
	);

});

export default EnemyGrid;