import React, { useState, useRef, forwardRef, useImperativeHandle } from 'react';

import PlayerDeck from './playerDeck.js';

const PlayerGrid = forwardRef((props, ref) => {

	const [currentMode, setCurrentMode] = useState('player');

	const playerDeckRefArray = [useRef(), useRef(), useRef(), useRef()];
	

	useImperativeHandle(ref, () => ({
		changeCurrentMode: (newMode) => {
			setCurrentMode(newMode);
			for (let i = 0; i < playerDeckRefArray.length; i ++) {
				playerDeckRefArray[i].current.changeCurrentMode(newMode);
			}
		},

		addToAvailableDeck: (cardNum) => {
			for (let i = 0; i < playerDeckRefArray.length; i ++) {				
				let hasSuccessfullyAdded = playerDeckRefArray[i].current.addCardToDeck(cardNum);

				if (hasSuccessfullyAdded) return;
			}			
		},
		
	}));

	const removeFromDeckAndMakeAvailable = (cardNum) => {
		props.cardSelectorRef.current.removeCardFromPlayerUsedList(cardNum)
	}

	const getPlayerDeck = (deckNum) => {
		return playerDeckRefArray[deckNum].current;
	}

	return(
		<div style={{backgroundColor: '#3cd'}} className="roundSquare">
			{ [0, 1, 2, 3].map(n => <PlayerDeck key={String(n)} ref={playerDeckRefArray[n]} deckNum={n} funcUtils={{'removeFromDeckAndMakeAvailable': removeFromDeckAndMakeAvailable, 'getPlayerDeck': getPlayerDeck}}/>) }
		</div>
	);

});

export default PlayerGrid;