import React from 'react';

import cards from '../utils/card-data.js';
import emptyCard from '../img/enemy-empty-card.png';

const EnemyCard = (props) => {
	
	const cardStyle = {
		width: '20%'
	}

	return (
		<span key={String(props.position) + "#enemyCard"}>
			<img style={{width: '20%', opacity: props.transparent ? '0.5' : '1.0'}} src={props.position >= 0 ? cards['items'][props.position]['iconUrls']['medium'] : emptyCard} />
		</span>
	);
}


export default EnemyCard;